@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap');


.lote{
    display: grid;
    width: 90%;
    height: 230px;
    grid-template: repeat(4, 1fr) / 306.66px 1fr 1fr;
    align-items: stretch;
    align-content: stretch;
    justify-items: center;
    border-radius: 7px ;
    margin: 10px;
    box-shadow: 1px 1px 13px 0px rgba(0,0,0,0.75);
    overflow: hidden;
}

.lote img {
    max-height: 230px;
    grid-area: 1 / 1 / 5 / 2;
    border-radius: 5px 0 0 5px;
    border-right: 1px solid rgba(0, 0, 0, 0.319);
}

.lote-div{
    width: 90%;
    height: fit-content;
    align-self: center;
    font-family: 'Montserrat', sans-serif;
}

.lote-div span{
    font-size: 1.3em;
    font-weight: 500;
}

.categoria {
    grid-area: 1 / 2 / 2 / 4;
    font-size: 2em;
    font-weight: 700;
    background-color: #0d6312;
    color: white;
    border-radius: 7px 7px 0 0;
}

.raza {
    grid-area: 2 / 2 / 3 / 4;
    font-size: 1.7em;
    font-weight: 700;
}

.cabezas {
    grid-area: 3 / 2 / 4 / 3;
}

.peso{
    grid-area: 3 / 3 / 4 / 4;
}

.ubicacion{
    grid-area: 4 / 2 / 5 / 4;
}